.container {
  margin: 20px;
}

.headerDiv {
  background-color: #ffd194;
  color: darkred;
  font-size: 12px;
  margin: auto;
  max-width: 50%;
  opacity: 1;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
}

.titleStyles {
  color: brown;
  font-size: 22px;
  text-align: center;
}

.timeDateDependents > p {
  font-size: 17px;
  margin: 0px;
  padding: 5px;
}

.selPara {
  padding-right: 5px;
}

.sideBySide {
  display: flex;
  flex-wrap: wrap;
}

.sideBySide hr {
  width: 75%;
}

.col1,
.col2 {
  background: #70e1f5; 
	background: linear-gradient(to bottom, #ffd194, #70e1f5);
  color: rgb(0, 0, 0);
  font-size: 16px;
  margin-top: 20px;
  margin: 2%;
  margin-bottom: 0;
  opacity: 0.95;
  padding-bottom: 30px;
  position: relative;
  text-align: center;
  border-radius: 5px;
}

.col1 {
  flex: 1 1 160px;
}

.col1 ul {
  display: table;
  margin: 0 auto;
}

.col1 ul li {
  text-align: left;
}

.col2 {
  flex: 1 1 160px;
  justify-content: space-around;
}

label {
  display: inline-block;
}

input#amount {
  padding: 2%;
  margin-right: 10%;
}

.formLabel {
  display: inline-block;
  width: 30px;
  text-align: center;
  margin: 2%;
}

select {
  display: inline-block;
  margin: 2%;
  border-radius: 5px;
  padding: 5px;
}

input {
  margin: 2%;
  border-radius: 5px;
  padding: 5px;
}

.convertButton {
  padding: 1%;
  border: 5%;
  border-radius: 5px;
}

.convertResult {
  background-color: rgb(181, 137, 231);
  color: black;
  display: block;
  margin: 0 auto;
  max-width: 50%;
  margin-top: 3%;
  padding: 10%;
  border-radius: 25px;
  font-size: 30px;
  font-weight: bold;
}

.footer {
  background-color: #a1a3cc;
  bottom: 0;
  color: rgb(0, 0, 0);
  left: 0;
  margin-bottom: -16px;
  min-height: 60px;
  padding: 5px;
  position: relative;
  text-align: center;
  width: 100%;
}

body {
  max-width: 100%;
  margin: auto;
  padding: 0;
  /* background-color: blueviolet; */
  font-family: "Roboto", Montserrat, Open Sans;
  /* background-image: url("https://ak2.picdn.net/shutterstock/videos/21851902/thumb/1.jpg"); */

  /* background-image: url("https://ak3.picdn.net/shutterstock/videos/20769583/thumb/1.jpg"); */

  background-image: url(/Exchange-rate/static/media/bgi.dd1a356a.webp);

   /* Animated backgrounds: */

  /* background-image: url("https://thumbs.gfycat.com/ParallelTiredBarasinga-size_restricted.gif");

  background-image: url("http://subseaexplorer.net/wp-content/uploads/2016/06/stock-market-loss-lawyer-header-video.gif");  */

  background-repeat: no-repeat;
  background-size: cover;
  /*For parallax effect */
  height: 100%;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

